













import Vue from 'vue';

export default Vue.extend({
  name: 'EditableMark',
  props: {
    edit: Boolean,
    value: Boolean,
    group: String,
    multiple: Boolean,
  },
  methods: {
    checkHandler(e: InputEvent) {
      this.$emit('input', (e.target as HTMLInputElement).checked);
    },
  },
})
